<template>
  <b-overlay :show="show" rounded="sm">
  <!--<div class="container">--><!--  class : container -->
  <b-container>
    <div style="height:15px;"></div>
    <h1 class="text-center"> </h1>    
    <div style="height:5px;"></div>
    <!-- <div style='text-align:center;color:#ff0000;'>
      {{$moment().format('YYYYMMDD dddd')}}  
    </div> -->
    <div style="text-align:right;">
      <b-button @click="select"></b-button>
    </div>
    <div>
      <b-form-group 
        label=" " 
        label-cols-lg="2" 
        v-slot="{ ariaDescribedby }"
      >
        <b-form-radio-group
          :aria-describedby="ariaDescribedby"
          class="mt-lg-2"
          v-model="form1.profitLossGubun"
        >
          <!--<b-form-radio :value="null" inline></b-form-radio>-->
          <b-form-radio value="" inline></b-form-radio>
          <b-form-radio value="profit" inline></b-form-radio>
          <b-form-radio value="loss" inline></b-form-radio>
        </b-form-radio-group>
      </b-form-group>
      <!-- 
        responsive    . 
        se1         .   . 
        se1  responsive         .     .
      -->
      <b-table 
        id="my-table" 
        hover 
        bordered
        head-variant="dark"
        :items="items" 
        :fields="fields" 
        :tbody-tr-class="rowClass" 
        responsive 
        :per-page="perPage"
        :current-page="currentPage"        
      >
        <template #cell(TRADE_PROFIT_LOSS_AFTER_TAX_AMT)="data">
          <span v-html="data.value"></span>
        </template>
        <template #cell(STOCK_RETURN_RATE)="data">
          <span v-html="data.value"></span>
        </template>
      </b-table>
    </div>
    <div style="height:5px;"></div>
    <div>
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="my-table"
        align="center"
      ></b-pagination>    
    </div>
  </b-container>
  <!--</div>--><!--  class : container -->
  </b-overlay>
</template>

<script>
import axios from 'axios';

export default {
  //  ,    .
  computed: {
    rows() {
      return this.items.length
    },
  },
  //         .
  // created      . -> [Vue warn]: Error in created hook: "TypeError: handler.call is not a function"  
  // created: {
  // }, 
  //     . 
  mounted() {    
    // Vue.js   UI   nextTick     .
    //      .
    //     .
    this.$nextTick(function () {
      console.log(navigator.userAgent.toLowerCase());
      var mobile = (/iphone|ipad|ipod|android/i.test(navigator.userAgent.toLowerCase()));
      if (mobile) { 
        // 
        this.fields[1] = null;//
        this.fields[2].label = '';
        //this.fields[3] = null;
        this.fields[3].label = '';
        this.fields[4] = null;//

        this.perPage = 5; //  
      }else{
        // 
      }      
      this.select();
    }) 
  },
  components: {
    // TodoList,
    // AddTodo,
  },
  data() {
    return {
      form1: {
        profitLossGubun: '',
        fileSelf:null,
      },      
      perPage: 10,
      currentPage: 1,      
      show: false,
      fields: [
        { 
          key: 'TRADE_DT2',
          label: '',
          sortable: false,
          class: 'text-center'
        },
        {
          key: 'TRADE_AMT',
          label: ' ()',
          sortable: false,
          class: 'text-right',
          formatter: value => {
            // 
            var value2 = value;
            value2 = String(value).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            return value2;
          },
        },
        {
          key: 'TRADE_PROFIT_LOSS_AFTER_TAX_AMT',
          label: ' ()',
          sortable: false,
          class: 'text-right',
          formatter: value => {
            // 
            var value2 = value;
            value2 = String(value).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            if (value > 0) {
              value2 = "<font color=red>"+value2+"</font>";
            } else if (value < 0) {
              value2 = "<font color=blue>"+value2+"</blue>";
            }
            return value2;
          },          
        },
        {
          key: 'STOCK_RETURN_RATE',
          label: ' (%)',
          sortable: false,
          class: 'text-right',
          formatter: value => {
            if (value > 0) {
              return "<font color=red>"+value+"</font>";
            } else if (value < 0) {
              return "<font color=blue>"+value+"</blue>";
            } else {
               return value;
            }
          },          
        },
        {
          key: 'TRADE_CNT',
          label: ' ()',
          sortable: false,
          class: 'text-right'
          // Variant applies to the whole column, including the header and footer
          //variant: 'danger'
        }
      ],
      items: [
        // { age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
        // { age: 21, first_name: 'Larsen', last_name: 'Shaw' },
        // { age: 89, first_name: 'Geneva', last_name: 'Wilson' },
        // { age: 38, first_name: 'Jami', last_name: 'Carney' }
      ],
      //todo_not_found_msg: " ..",
      
    }
  }, 
  methods: {
    rowClass(item, type) {
      var moment = require('moment'); // require
      var today = moment().format('YYYY-MM-DD')
      
      if (!item || type !== 'row') return
      if (item.TRADE_DT2 === today) return 'table-success'
    },    
    select() {

      console.log(this.form1.profitLossGubun);

      this.show = true;

      var param = {};
      param.profitLossGubun = this.form1.profitLossGubun;//

      axios.get("/api/auto/dayProfit/select", {
        params: param
      }).then((response) => {

        this.items = response.data;
        
        this.show = false;

      }).catch((error) => {
        console.log(error);
      });      
    },
  }
}
</script>

